/* Global */
html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto Condensed", sans-serif;
  background-color: #0f0f0f;
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh; /* Asegura que ocupe toda la altura de la ventana */
  margin: 0; /* Elimina el margen predeterminado */
  font-size: 1.5rem;
  line-height: 1.5;
}

h1 {
  font-size: 3rem;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

.content {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

/* Layout */
.Complet-Box {
  padding: 5rem;
  background-color: #312f2f;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  width: 80%;
}
@media (min-width: 992px) {
  .Complet-Box {
    width: 25%;
  }
}
.Complet-Box .Personal-Data {
  display: flex;
  flex-direction: column;
  align-items: anchor-center;
}
.Complet-Box .Personal-Data img {
  width: 100px;
  height: 100px;
  border-radius: 100%;
}
.Complet-Box .Personal-Data .Location-Data {
  color: #87cf66;
  font-weight: bold;
}
.Complet-Box .Contact-Data .Contact-Data-Btn {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.Complet-Box .Contact-Data .Contact-Data-Btn a {
  background-color: #3a3838;
  padding: 1rem;
  display: block;
  width: 100%;
  text-align: center;
  margin-top: 1rem;
  color: #fff;
  font-weight: bold;
  border-radius: 5px;
}
.Complet-Box .Contact-Data .Contact-Data-Btn a:hover {
  background-color: rgb(52.2, 50.4, 50.4);
}